@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@500&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-roboto: Roboto;
  --font-inter: Inter;

  /* font sizes */
  --font-size-2xs: 11px;
  --font-size-sm: 14px;
  --font-size-7xl-2: 26.2px;
  --font-size-3xs: 10px;
  --font-size-xs: 12px;

  /* Colors */
  --studio-lightmode-lightbg-f8f9fb: #f8f9fb;
  --studio-darkmode-popuplabels-5e6a86: #5e6a86;
  --studio-darkmode-maincta-457eff: #457eff;
  --studio-darkmode-popupclick-40495e: #40495e;
  --studio-darkmode-popupbg-2b364d: #2b364d;
  --studio-darkmode-allwhite-ffffff: #fff;
  --studio-lightmode-secondarybutton-ebf0fb: #ebf0fb;
  --studio-lightmode-lightseparator-ecf0f6: #ecf0f6;
  --studio-darkmode-enabledlabel-e8effd: #e8effd;
  --studio-universal-confirm-4fc786: #4fc786;
  --studio-darkmode-normalsolid-icons-96a2be: #96a2be;
  --studio-darkmode-bg-top-1d2434: #1d2434;
  --studio-darkmode-bg-main-161c29: #161c29;
  --color-darksalmon: #e19969;
  --studio-darkmode-explainertext-485269: #485269;

  /* Gaps */
  --gap-sm: 14px;
  --gap-xl: 20px;
  --gap-sm-1: 13.1px;
  --gap-8xs: 5px;
  --gap-5xl: 24px;
  --gap-7xs: 6px;

  /* Paddings */
  --padding-10xs: 3px;
  --padding-8xs: 5px;
  --padding-2xl-8: 21.8px;
  --padding-mid-4: 17.4px;
  --padding-3xs: 10px;
  --padding-5xs: 8px;
  --padding-6xs: 7px;

  /* border radiuses */
  --br-10xs: 3px;
  --br-sm-1: 13.1px;
  --br-9xs: 4px;
  --br-sm: 14px;
  --br-11xs: 2px;
}
